footer
    background: #000
    height: 210px
    padding: 16px

.footer-body
    display: flex
    justify-content: space-between
    align-items: center

.footer-logo
    width: 179px
    height: 177px

.footer-info,
.footer-curses
    max-width: 162px
    list-style: none
    margin-top: 12px
    &__title
        color: #fff
        font-weight: bold
        font-size: 14px
        line-height: 16px
        margin-bottom: 11px

.footer-curse, .footer-info-list
    margin-bottom: 11px

.footer-contact
    max-width: 300px

.contacts-email, .contacts-phone
    margin-bottom: 21px